.logo {
  line-height: 1em;
  display: block;
  color: $white;
  @include heading-features;
  height: 60px;
  width: 60px;
  font-size: 10px;
  padding: 0.5em;
  transition: background 400ms ease-in-out, color 400ms ease-in-out;
  pointer-events: auto;

  &:hover {
    background-color: $color-accent;
    color: $white;
  }

  &.invert {
    background-color: $white !important;
    color: $color-accent !important;
  }

  &.black {
    color: rgba($off-black, 0.5);
  }

  &.initial {
    background: $color-accent;
    color: $white;
  }

  .home &.initial {
    background: transparent;
    color: $white;
    box-shadow: inset 0 0 0 2px $white;

    &:hover {
      background-color: transparent;
    }

    @media screen and (max-width: $small-vp) {
      background: $color-accent;
      box-shadow: none;
    }
  }
}

.heading-inner {
  position: fixed;
  top: 20px;
  left: 20px;
  right: 30px;
  pointer-events: none;
  z-index: $z-foreground;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (max-width: $small-vp) {
    left: 20px;
    right: 20px;
    top: 10px;
  }
}

.header-items-wrapper {
  display: flex;
  width: calc(30vw - 30px);
  @media screen and (max-width: $small-vp) {
    justify-content: flex-end;
    width: 50%;
  }
}

.header-item-link {
  padding: 0 3em 0 0;

  @media screen and (max-width: $small-vp) {
    &:last-child {
      padding-right: 0;
    }
  }
}

.header-item {
  padding: 0;
  line-height: 1em;
  pointer-events: auto;
  color: $color-accent;
  font-family: $sans-serif-light;
  white-space: nowrap;
  @include heading-features;
  @include rsp-font(2.5vw, 18px);
  transition: color 200ms ease-in-out;
  
  &:hover {
    color: darken($color-accent, 20%);
  }

  &.invert {
    color: $white;
  }

  &.inactive {
    color: darken($color-accent, 20%);
  }
}



